.home {

    .homeContent {
      margin: 6px 0 6px 12px;
      background: linear-gradient(75deg, $color-1 35%, transparent 90%);
      min-height: $minHeight;
      width: 100%;
      min-height: 500px;
      box-shadow: 0 0 0 1px $color-4, 0 0 0 2px $color-3, 0 0 0 3px $color-4;
      position: relative;
      overflow: hidden;
      
      &:after {
        content: '';
        position: absolute;
        height: 100%;
        width: inherit;
        background: url('../../media/matronche2.jpg') no-repeat center/cover;
        background-position: 70px -200px;
        z-index: -1;
        filter: brightness(80%);
        
      }
  
      .content {
        position: absolute;
        top: 15%;
        left: 10%;
        font-family: $font-1;
        
        h1 {
          font-size: 5rem;
          font-family: $font-3;
          color: $color-5;
          text-transform: uppercase;
          text-shadow: 3px 6px 2px $black;
        }
        h2 {
          font-size: 2.5rem;
          padding: 1.5rem 0 7rem;
          color: $color-3;
          text-shadow: 1px 4px 1px $black;
        }
        
          a {
            font-size: 1.2rem;
            color: white;
            padding: 1rem 2rem;
            border-radius: $borderRadius;
            box-shadow: 0 5px 1px rgb(15, 15, 15);
            transition: 0.2s ease;
            
            &:hover {
              background: $color-4;
            }
          
        }
      }
    }
  }
  @media screen and (max-width: 850px){
    .sidebar {
      margin-bottom: 10px !important ;
    }
  }
  @media screen and (max-width:580px){
    .homeContent {
      &:after {
      background: url('../../media/matronche2.jpg') no-repeat 40% 30%/cover !important;
      }
      h1 {
        font-size: 3rem !important;
      }
      h2 {
        font-size: 1.8rem !important;
      }
    }
  }
  body {
    background: rgb(31, 30, 30);
  }